@import "../../index.scss";

$bg-insya-allah: url("../../../assets/img/InsyaAllah.png");

.Second-Page {
    .Section {
        .Akad {
            width: 100%;
            text-align: center;
            padding-bottom: 20px;

            .InsyaAllah {
                width: 100%;
                height: 130px;
                @include bg-img($bg-insya-allah);
                background-size: contain;
            }

            .Title {
                font-family: CampanaScript;
                font-size: 35px;
                letter-spacing: 1px;
                color: var(--blue);
                margin-bottom: 10px;
            }

            .Hari,
            .Jam {
                display: flex;
                align-items: center;
                justify-content: center;
            }

            .Hari {
                font-size: 15px;
                line-height: 15px;
                margin-bottom: 5px;
            }

            .Jam {
                font-size: 15px;
                line-height: 15px;
            }
        }
    }
}
