.Second-Page {
    .Section {
        .Resepsi {
            width: 100%;
            text-align: center;
            padding-bottom: 20px;

            .Title {
                font-family: CampanaScript;
                font-size: 35px;
                letter-spacing: 1px;
                color: var(--blue);
                margin-bottom: 10px;
            }

            .Hari,
            .Jam {
                display: flex;
                align-items: center;
                justify-content: center;
            }

            .Hari {
                font-size: 15px;
                line-height: 15px;
                margin-bottom: 5px;
            }

            .Jam {
                font-size: 15px;
                line-height: 15px;
            }
        }
    }
}
