@import "../index.scss";

$box-bg: url("../../assets/img/Background-2.jpg");
$mempelai-foto: url("../../assets/img/Mempelai.jpg");
$frame-bg: url("../../assets/img/Frame-Round-1.png");

.First-Page {
    width: 100%;
    height: 100vh;
    left: 0px;
    top: 0px;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: hidden;
    opacity: 0;
    @include bg-img($box-bg);
    &:is(.First-Page-Active) {
        visibility: visible;
        opacity: 1;
    }

    .Box {
        width: 80vw;
        background-color: rgba($color: #ffffff, $alpha: 0.8);
        border: 1px solid rgb(172, 172, 172);
        border-radius: 20px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding-top: 20px;
        padding-bottom: 20px;
        overflow: hidden;

        .Walimatul-Ursy {
            font-size: 22px;
            line-height: 22px;
            margin-bottom: 20px;
            font-family: TheConstellation;
            color: var(--dark-grey);
            letter-spacing: 1px;
        }

        .Mempelai-Wanita,
        .Mempelai-Pria {
            font-size: 40px;
            line-height: 40px;
            color: var(--blue);
            font-family: Cultural;
            letter-spacing: 2px;
            position: relative;
            visibility: hidden;
            opacity: 0;
            @include Default-Anim(0.5s, inherit);

            &:is(.Mempelai-Wanita) {
                transform: translateX(-100px);

                &:is(.Mempelai-Wanita-Active) {
                    visibility: visible;
                    opacity: 1;
                    transform: translateX(-0px);
                    animation-name: Mempelai-Wanita-Anim;
                    @keyframes Mempelai-Wanita-Anim {
                        0% {
                            visibility: hidden;
                            opacity: 0;
                            transform: translateX(-100px);
                        }
                        40% {
                            transform: translateX(50px);
                        }
                        100% {
                            visibility: visible;
                            opacity: 1;
                            transform: translateX(-0px);
                        }
                    }
                }
            }

            &:is(.Mempelai-Pria) {
                transform: translateX(100px);

                &:is(.Mempelai-Pria-Active) {
                    visibility: visible;
                    opacity: 1;
                    transform: translateX(0px);
                    animation-name: Mempelai-Pria-Anim;
                    @keyframes Mempelai-Pria-Anim {
                        0% {
                            visibility: hidden;
                            opacity: 0;
                            transform: translateX(100px);
                        }
                        40% {
                            transform: translateX(-50px);
                        }
                        100% {
                            visibility: visible;
                            opacity: 1;
                            transform: translateX(0px);
                        }
                    }
                }
            }

            span {
                font-family: Cultural;
                opacity: 0;
                transform: translateY(50px) translateX(10px);
                display: inline-block;
                color: #fff;
                @include Default-Anim(0.5s, inherit);
                &:is(.spacer) {
                    display: inline-block;
                    width: 5px;
                }
                &:is(.fade) {
                    opacity: 1;
                    transform: translateY(0px) translateX(0px);
                    color: var(--blue);
                    animation-name: Fade-Anim;
                    @keyframes Fade-Anim {
                        0% {
                            opacity: 0;
                            color: #fff;
                            transform: translateY(50px) translateX(10px);
                        }
                        30% {
                            color: #37e3f0;
                            transform: translateY(-10px) translateX(10px);
                        }
                        80% {
                            transform: translateY(10px) translateX(-5px);
                        }
                        100% {
                            color: #4227a3;
                            opacity: 1;
                            transform: translateY(0px) translateX(0px);
                        }
                    }
                }
            }
        }

        .Mempelai-And {
            font-size: 20px;
            line-height: 20px;
            margin-top: 7px;
            color: var(--light-grey);
            font-family: TheConstellation;
            visibility: hidden;
            opacity: 0;
            transform: translateY(-25px);
            position: relative;
            @include Default-Anim(0.4s, inherit);
            &:is(.Mempelai-And-Active) {
                visibility: visible;
                opacity: 1;
                animation-name: Mempelai-And-Anim;
                transform: translateY(-0px);
                @keyframes Mempelai-And-Anim {
                    0% {
                        visibility: hidden;
                        opacity: 0;
                        transform: translateY(-5px);
                    }
                    90% {
                        transform: translateY(5px);
                    }
                    100% {
                        visibility: visible;
                        opacity: 1;
                        transform: translateY(-0px);
                    }
                }
            }
        }

        .Mempelai-Foto {
            margin-top: 20px;
            .Foto {
                width: 0px;
                height: 0px;
                border-radius: 50%;
                left: 50%;
                position: absolute;
                transform: rotate(-190deg) translateX(50%);
                @include bg-img($mempelai-foto);
                @include Default-Anim(0.4s, inherit);
                &:is(.Foto-Active) {
                    visibility: visible;
                    opacity: 1;
                    transform: rotate(-0deg) translateX(-50%);
                    width: 140px;
                    height: 140px;
                    animation-name: Foto-Active;
                }
                @keyframes Foto-Active {
                    0% {
                        visibility: hidden;
                        opacity: 0;
                        transform: rotate(-190deg) translateX(50%);
                        width: 0px;
                        height: 0px;
                    }
                    100% {
                        visibility: visible;
                        opacity: 1;
                        transform: rotate(-0deg) translateX(-50%);
                        width: 140px;
                        height: 140px;
                    }
                }
            }

            .Frame {
                position: relative;
                z-index: 1;
                width: 0px;
                height: 0px;
                visibility: hidden;
                opacity: 0;
                transform: rotate(180deg) translateY(-14px) translateX(-3px);
                @include bg-img($frame-bg);
                @include Default-Anim(0.4s, inherit);
                &:is(.Frame-Active) {
                    visibility: visible;
                    opacity: 1;
                    width: 180px;
                    height: 180px;
                    transform: rotate(0deg) translateY(-14px) translateX(-3px);
                    animation-name: Frame-Active;
                }
                @keyframes Frame-Active {
                    0% {
                        visibility: hidden;
                        opacity: 0;
                        transform: rotate(180deg) translateY(-14px)
                            translateX(-3px);
                        width: 0px;
                        height: 0px;
                    }
                    100% {
                        visibility: visible;
                        opacity: 1;
                        transform: rotate(0deg) translateY(-14px)
                            translateX(-3px);
                        width: 180px;
                        height: 180px;
                    }
                }
            }
        }

        .Kepada-Yth {
            font-size: 13px;
            line-height: 13px;
            margin: 30px 0px 0px 0px;
            color: var(--dark-grey);
        }

        .Kepada-Target-Nama {
            color: var(--dark-grey);
            margin: 5px 0px 5px 0px;

            &:not(.Gelar) {
                font-size: 22px;
                line-height: 22px;
                font-family: AdorableHandmade;
            }

            .Gelar {
                font-size: 15px;
                line-height: 15px;
                font-weight: bold;
            }
        }

        .Kepada-Target-Alamat {
            font-size: 13px;
            line-height: 13px;
            color: var(--dark-grey);
            font-style: italic;
        }

        .Buka-Undangan {
            font-size: 15px;
            line-height: 15px;
            font-weight: bold;
            border: none;
            outline: none;
            cursor: pointer;
            background-color: var(--blue);
            border-radius: 27.5px;
            color: #eee;
            height: 45px;
            width: 170px;
            margin-top: 30px;
            position: relative;
            visibility: hidden;
            opacity: 0;
            transform: translateY(100px);
            @include Default-Anim(0.4s, inherit);
            &:active {
                opacity: 0.8;
            }

            &:is(.Buka-Undangan-Active) {
                visibility: visible;
                opacity: 1;
                width: 170px;
                transform: translateY(0px);
                animation-name: Buka-Undangan-Anim;
                @keyframes Buka-Undangan-Anim {
                    0% {
                        width: 170px;
                        transform: translateY(100px);
                        visibility: hidden;
                        opacity: 0;
                    }
                    50% {
                        transform: translateY(0px);
                    }
                    70% {
                        width: 250px;
                        transform: translateY(10px);
                    }
                    100% {
                        width: 170px;
                        transform: translateY(0px);
                        visibility: visible;
                        opacity: 1;
                    }
                }
            }
        }
    }
}
