@import "../index.scss";

.RBX-Loading {
    width: 100%;
    height: 100vh;
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .Loading-Text {
        width: 100%;
        text-align: center;
        .Head {
            font-size: 40px;
            letter-spacing: 2px;
            font-family: TheConstellation;
            color: #fff;
            span {
                font-family: TheConstellation;
                position: relative;
                visibility: hidden;
                opacity: 0;
                display: inline-block;
                transform: translateY(-50px);
                color: rgb(0, 204, 255);
                transition: 0.3s all ease;
                &:is(.spacer) {
                    display: inline-block;
                    width: 5px;
                }
                &:is(.fade) {
                    visibility: visible;
                    opacity: 1;
                    transform: translateY(-0px);
                    color: var(--blue);
                }
            }
        }
    }
}
