@import "../../index.scss";

.Second-Page {
    .Section {
        .Hadiah {
            width: 100%;
            position: relative;

            .Title {
                font-family: CampanaScript;
                font-size: 35px;
                color: var(--blue);
                margin-bottom: 20px;
            }

            .Ucapan {
                font-size: 15px;
            }

            .Bank {
                width: 100%;
                margin-top: 30px;
                margin-bottom: 30px;

                .Bank-Box {
                    border-radius: 10px;
                    padding: 5px 10px 20px 10px;
                    background-color: rgba(
                        $color: rgb(10, 86, 173),
                        $alpha: 0.2
                    );

                    .Bank-Label {
                        left: 50%;
                        font-size: 15px;
                        height: 32px;
                        line-height: 28px;
                        padding: 0px 30px 0px 30px;
                        border-radius: 16px;
                        font-weight: bold;
                        color: #fff;
                        background-color: var(--blue);
                        position: absolute;
                        border: 2px solid #fff;
                        transform: translateY(-20px) translateX(-50%);
                    }

                    .Bank-Owner {
                        margin-top: 20px;
                        margin-bottom: 5px;
                        font-size: 15px;
                        font-weight: bold;
                    }

                    .Bank-Value {
                        font-size: 17px;
                        height: 40px;
                        line-height: 40px;
                        font-weight: bold;
                        background-color: rgba(
                            $color: rgb(10, 86, 173),
                            $alpha: 0.7
                        );
                        color: #fff;
                        width: 100%;
                        text-align: center;
                        border-radius: 20px;
                    }
                }
            }

            .Alamat {
                width: 100%;
                position: relative;
                margin-top: 30px;

                .Alamat-Box {
                    border-radius: 10px;
                    padding: 5px 10px 20px 10px;
                    background-color: rgba(
                        $color: rgb(10, 86, 173),
                        $alpha: 0.2
                    );
                    text-align: left;

                    .Alamat-Label {
                        left: 50%;
                        font-size: 15px;
                        height: 32px;
                        line-height: 28px;
                        padding: 0px 30px 0px 30px;
                        border-radius: 16px;
                        font-weight: bold;
                        color: #fff;
                        background-color: var(--blue);
                        position: absolute;
                        border: 2px solid #fff;
                        transform: translateY(-20px) translateX(-50%);
                    }

                    .Alamat-Value {
                        margin-top: 20px;
                        font-size: 14px;
                        background-color: rgba(
                            $color: rgb(10, 86, 173),
                            $alpha: 0.7
                        );
                        color: #fff;
                        width: 100%;
                        padding: 10px 15px 10px 15px;
                        border-radius: 15px;
                    }
                }
            }
        }
    }
}
