@import "../../index.scss";

$box-bg: url("../../../assets/img/Background-2.jpg");

.Second-Page {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    visibility: hidden;
    overflow-y: scroll;
    overflow-x: hidden;
    opacity: 0;

    padding: 500px 20px 200px 20px;

    @include bg-img($box-bg);
    @include Default-Anim(0.8s, inherit);
    &:is(.Second-Page-Active) {
        visibility: visible;
        opacity: 1;
        padding: 0px 20px 200px 20px;
        animation-name: Second-Page-Anim;
    }
    @keyframes Second-Page-Anim {
        0% {
            padding: 500px 20px 200px 20px;
        }
        100% {
            padding: 0px 20px 200px 20px;
        }
    }

    .Section {
        width: 100%;
        background-color: rgba($color: #fff, $alpha: 0.8);
        -webkit-box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);
        -moz-box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);
        box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);
        padding: 20px;
        border-radius: 7px;
    }

    .Page-Spacer {
        min-height: 10px;
    }
}
