@font-face {
    font-family: TheConstellation;
    src: url("../assets/fonts/TheConstellation.ttf");
}

@font-face {
    font-family: Cultural;
    src: url("../assets/fonts/Cultural.ttf");
}

@font-face {
    font-family: CampanaScript;
    src: url("../assets/fonts/CampanaScript.otf");
}

@font-face {
    font-family: AdorableHandmade;
    src: url("../assets/fonts/AdorableHandmade.ttf");
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

:root {
    --blue: rgb(10, 86, 173);
    --cyan: rgb(90, 213, 230);
    --dark-blue: rgb(45, 25, 156);
    --dark-grey: rgb(114, 114, 114);
    --light-grey: #9b9b9b;
}

@mixin bg-img($image: "") {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: $image;
}

@mixin Default-Anim($time, $iteration) {
    animation-duration: $time;
    animation-iteration-count: $iteration;
}

@mixin Rotate-Infinite($time, $direction) {
    @keyframes Rotator {
        from {
            transform: rotate(0deg);
        }
        to {
            @if $direction == "right" {
                transform: rotate(360deg);
            } @else {
                transform: rotate(-360deg);
            }
        }
    }
    -webkit-animation: Rotator $time linear infinite;
    -moz-animation: Rotator $time linear infinite;
    -ms-animation: Rotator $time linear infinite;
    -o-animation: Rotator $time linear infinite;
    animation: Rotator $time linear infinite;
}

body {
    width: 100%;
    height: 100vh;
    overflow-x: hidden;
    .Container {
        width: 100%;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        .Page {
            width: 100%;
            height: 100vh;
            max-width: 700px;

            // PC PAGE BORDER
            @media screen and (min-width: 700px) {
                border-left: 1px solid rgb(189, 189, 189);
                border-right: 1px solid rgb(189, 189, 189);
            }
        }
    }
}
