@import "../index.scss";

@mixin DefaultStyle {
    position: fixed;
    z-index: 5;
}

.Right-Navigation {
    @include DefaultStyle;
    right: -100px;
    top: 50%;
    transform: translateY(-50%);
    visibility: hidden;
    opacity: 0;
    padding-right: 5px;
    @include Default-Anim(0.5s, inherit);

    &:is(.Right-Navigation-Active) {
        visibility: visible;
        opacity: 1;
        right: 0px;
        animation-name: Right-Navigation-Anim;
        @keyframes Right-Navigation-Anim {
            0% {
                right: -100px;
                visibility: hidden;
                opacity: 0;
            }
            30% {
                visibility: visible;
                opacity: 1;
            }
            70% {
                right: 30px;
            }
            100% {
                right: 0px;
                visibility: visible;
                opacity: 1;
            }
        }
    }

    .Box {
        display: flex;
        align-items: center;
        flex-direction: column;

        .Button {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            height: 40px;
            width: 40px;
            border-radius: 50%;
            color: var(--blue);
            background-color: #fff;
            -webkit-box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);
            -moz-box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);
            box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);

            &:not(.Button:nth-last-child(1)) {
                margin-bottom: 20px;
            }

            &:is(.Music-Played) {
                position: relative;
                border: 2px solid var(--cyan);
                animation: Rotate-Anim 3s linear infinite;
                @keyframes Rotate-Anim {
                    0% {
                        transform: rotate(0deg);
                        border: 2px solid var(--cyan);
                    }
                    50% {
                        border: 2px solid var(--blue);
                    }
                    100% {
                        transform: rotate(360deg);
                        border: 2px solid var(--cyan);
                    }
                }
            }
        }
    }
}

.Bottom-Navigation {
    @include DefaultStyle;
    left: 50%;
    transform: translateX(-50%);
    bottom: 100px;
    visibility: hidden;
    opacity: 0;
    @include Default-Anim(0.5s, inherit);

    &:is(.Bottom-Navigation-Active) {
        visibility: visible;
        opacity: 1;
        bottom: 10px;
        animation-name: Bottom-Navigation-Anim;
        @keyframes Bottom-Navigation-Anim {
            0% {
                bottom: 0px;
                visibility: hidden;
                opacity: 0;
            }
            30% {
                visibility: visible;
                opacity: 1;
            }
            50% {
                bottom: 30px;
            }
            100% {
                bottom: 10px;
                visibility: visible;
                opacity: 1;
            }
        }
    }

    .Box {
        padding: 5px 15px 5px 15px;
        border-radius: 35px;
        background-color: var(--blue);
        display: flex;
        align-items: center;

        .Button {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            border-radius: 0px;
            color: #fff;
            height: 35px;
            width: 35px;
            transition: 0.3s all ease;
            background-color: rgba($color: #fff, $alpha: 0.2);

            &:is(.Button-Active) {
                border-radius: 27.5px;
                background-color: #fff;
                color: #000000;
                height: 45px;
                width: 100px;
            }

            &:nth-child(n + 2) {
                margin-left: 5px;
            }

            .Label {
                font-size: 0px;
                line-height: 0px;
                visibility: hidden;
                opacity: 0;
                transition: 0.3s all ease;
                user-select: none;

                &:is(.Label-Active) {
                    font-size: 10px;
                    line-height: 10px;
                    visibility: visible;
                    opacity: 1;
                }
            }
        }
    }
}
