@import "../index.scss";

.Second-Page {
    .Music-Player-Indicator {
        visibility: hidden;
        opacity: 0;
        position: fixed;
        z-index: 7;
        top: 0px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5px 0px 5px 0px;
        background-color: rgba($color: #000000, $alpha: .3);

        &:is(.Music-Player-Indicator-Active) {
            visibility: visible;
            opacity: 1;
        }

        .Music-Box {
            background-color: #fff;
            height: 20px;
            width: 50%;
            border-radius: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            -webkit-box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);
            -moz-box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);
            box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);

            .Track {
                width: 100%;
                font-size: 12px;
                line-height: 12px;

                strong {
                    color: var(--blue);
                }
            }

            .Music-Duration {
                font-size: 11px;
                line-height: 11px;
                padding: 2px 5px 2px 5px;
            }
        }
    }

    .Music-List {
        visibility: hidden;
        opacity: 0;
        position: fixed;
        z-index: 7;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        padding: 100px 40px 0px 40px;
        transition: 0.3s all ease;
        background-color: rgba($color: #000000, $alpha: 0.5);

        &:is(.Music-List-Active) {
            visibility: visible;
            opacity: 1;
            padding: 0px 40px 0px 40px;
        }

        .List {
            width: 100%;
            background-color: #fff;
            border-radius: 10px;
            overflow: hidden;

            .List-Head {
                width: 100%;
                letter-spacing: 1px;
                font-size: 18px;
                line-height: 33px;
                padding: 0px 10px 0px 10px;
                background-color: var(--blue);
                color: #fff;
                font-family: Cultural;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            .List-Body {
                width: 100%;
                height: 200px;
                overflow-y: scroll;
                padding-bottom: 20px;

                .Music {
                    width: 100%;
                    padding: 5px 10px 5px 10px;
                    border-bottom: 1px solid #c1c1c1;

                    &:is(.Music-Active) {
                        color: var(--blue);
                    }

                    .Artist {
                        font-size: 14px;
                        font-weight: bold;
                    }

                    .Title {
                        font-size: 12px;
                    }
                }
            }
        }
    }
}
