@import "../../index.scss";
$bg-jazakumullah: url("../../../assets/img/jazakumullah.png");

.Second-Page {
    .Section {
        .Lainnya {
            width: 100%;
            text-align: center;

            .Jazakumullah {
                width: 100%;
                height: 150px;
                @include bg-img($bg-jazakumullah);
                background-size: contain;
            }

            .Ucapan {
                font-size: 15px;
            }

            .Terimakasih {
                font-family: CampanaScript;
                font-size: 35px;
                letter-spacing: 1px;
                color: var(--blue);
                margin-top: 20px;
                margin-bottom: 30px;
            }

            .Hormat {
                font-size: 15px;
                margin-bottom: 15px;
            }

            .Hormat-Wanita {
                font-family: Cultural;
                font-size: 30px;
            }

            .Hormat-Dan {
                font-size: 17px;
                line-height: 17px;
                margin: 5px 0px 5px 0px;
            }

            .Hormat-Pria {
                font-family: Cultural;
                font-size: 30px;
            }
        }
    }
}
