.Second-Page {
    .Section {
        .Maps {
            width: 100%;
            text-align: center;
    
            .Title {
                font-family: CampanaScript;
                font-size: 35px;
                letter-spacing: 1px;
                color: var(--blue);
            }
    
            .Alamat {
                font-size: 15px;
                margin: 10px 0px 20px 0px;
            }
    
            .OpenMap {
                border: none;
                font-size: 14px;
                padding: 7px 10px 7px 10px;
                background-color: var(--blue);
                color: #eee;
                border-radius: 4px;
                margin: 30px 0px 30px 0px;
            }
        }
    }
}