@import "../index.scss";

$box-bg: url("../../assets/img/Curtain-Background.png");

.Curtain {
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 5;
    background-color: #9bcbeb;
    visibility: visible;
    opacity: 1;
    transition: 0.3s all ease;
    display: flex;
    align-items: center;
    justify-content: center;
    @include Default-Anim(0.8s, inherit);
    &:is(.Curtain-Active) {
        visibility: hidden;
        opacity: 0;
        width: 0px;
        height: 0px;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        animation-name: Curtain-Anim;
    }
    @keyframes Curtain-Anim {
        0% {
            width: 100%;
            height: 100vh;
        }
        100% {
            width: 0px;
            height: 0px;
            left: 50%;
            top: 50%;
            transform: translateX(-50%) translateY(-50%);
        }
    }

    .Box {
        height: 100vh;
        width: 100vw;
        visibility: hidden;
        opacity: 0;
        transform: translateX(0vw);
        @include Default-Anim(1s, inherit);
        @include bg-img($box-bg);
        &:is(.Box-Active) {
            animation-name: Box-Anim;
            visibility: hidden;
            opacity: 0;
            transform: translateX(100vw);
        }
        @keyframes Box-Anim {
            0% {
                visibility: hidden;
                opacity: 0;
                transform: translateX(0vw);
            }
            50% {
                visibility: visible;
                opacity: 1;
            }
            70% {
                transform: translateX(0vw);
            }
            90% {
                visibility: visible;
                opacity: 1;
            }
            100% {
                transform: translateX(100vw);
                visibility: hidden;
                opacity: 0;
            }
        }
    }
}
